*, *::after, *::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e9e9e9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #2A85B3;
  text-decoration: none;
}

a:hover,
a:focus-visible {
  text-decoration: underline;
}

nav.navbar,
nav.navbar * {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

nav.navbar {
  padding: 1rem 2rem;
  background-color: #2A85B3;
  color: #ffffff;
  border-radius: 0 0 12px 12px;
}

nav.navbar * {
  margin-left: auto;
  margin-right: auto;
}

nav.navbar *:first-child {
  flex-grow: 0;
  flex-shrink: 0;
}

nav.navbar .links {
  gap: 0.5rem;
}

nav.navbar img {
  height: 2rem;
  width: auto;
  margin: 0;
}

nav.navbar a {
  color: inherit;
  text-decoration: none;
  padding: 0.5rem 1rem;
  position: relative;
  border-radius: 6px 6px 0px 0px;
  transition: background-color 100ms
}

nav.navbar a::after {
  position: absolute;
  left: 50%;
  right: 50%;
  bottom: 0;
  height: 2px;
  content: '';
  background-color: #fafafa00;
  transition: all 100ms, background-color 100ms;
}

nav.navbar a:hover {
  background-color: #fafafa33;
}

nav.navbar a:hover::after,
nav.navbar a:focus-visible::after {
  left: 0;
  right: 0;
  background-color: #fafafa;
}

.content {
  padding: 2rem;
}

.blog-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.blog-preview {
  background-color: #fafafa;
  border-radius: 12px;
  display: flex;
  flex-direction: row;
  min-height: 8rem;
  overflow: hidden;
  box-shadow: 1px 3px 7px -1px rgba(100,100,200,0.1);
  transition: transform 200ms;
}

.blog-preview:hover {
  transform: scale(1.02);
}

.blog-preview-image {
  width: 100%;
  max-width: 8rem;
  background-color: #4a9de5;
}

.blog-preview article {
  padding: 2rem;
  color: #777777;
}
.blog-preview article p {
  color: #555555;
}
.blog-preview article h3,
.blog-preview article h4 {
  color: #777777;
}
.blog-preview article h2 {
  color: #555555;
}
.blog-preview .summary {
  color: #666666;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
#new-blog {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
#blog-editor {
  background-color: #fafafa;
  border-radius: 6px;
  padding: 1rem;
  box-shadow: 1px 3px 7px -1px rgba(100,100,200,0.1);
}

button {
  max-width: fit-content;
  padding: 0.5rem 2rem;
  border-radius: 99999px;
  border: 2px solid #4a9de5;
  color: #4a9de5;
  background-color: #eeeeee;
  box-shadow: 0px 0px 16px -6px rgba(0,146,255,0.75);
  cursor: pointer;
  transition: background-color 50ms, color 50ms, box-shadow 200ms, transform 200ms;
}
button:hover {
  color: white;
  background-color: #4a9de5;
  box-shadow: 0px 0px 16px -2px rgba(0,146,255,0.75);
  transform: scale(1.07);
}